@import url('https://fonts.googleapis.com/css?family=Merriweather:regular,bold,italic');
@import url('https://fonts.googleapis.com/css?family=Open+Sans');

* {
  font-family: 'Open Sans';
}

.tab-container {
  height: 8vh;
  background-color: #008080;
  display: flex;
  width: 100%;
  margin: auto;
}

.tab-container, .body {
  
  justify-content: center;
  align-items: center;
}

.body {
  flex: 1;
  flex-direction: column;
  display: flex;
  text-align: center;
  margin: 2%;
  width: 96%;
}

.tab-button {
  height: 30%;
  width: 10%;
  padding: 1%;
  margin: 0.5%;
  border-radius: 5px;
  border-style: hidden;
  
  text-align: center;
  text-decoration: none;
  color: white;
  background-color: #008080;
}

.tab-button:hover {
  background-color: #ffffff;
  color: #008080;
}

.container {
  background-color: #ffffff;
}

.profile-photo {
  width: 15%;
  height: 15%;
}

.name {
  font-size: 2.5rem;
}

.preview-items-container {
  flex: 1;
  flex-direction: column;
  display: flex;
  height: 40%;
  width: 100%;
  border: 2px solid #008080;
  border-radius: 10px;
  background-color: #008080;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: 0.5%;
  text-decoration: none;
  padding-bottom: 2%;
  box-shadow: 5px 8px #c9c9c9;
}

.text {
  color: #ffffff;
  text-decoration: none;
}

.preview-container {
  flex: 1;
  min-height: 10%;
  max-height: 10%;
  min-width: 30%;
  max-width: 30%;
  margin: 1%;
}

.previews-container {
  flex: 1;
  flex-direction: row;
  flex-wrap: wrap;
  display: inline-flex;
  align-items: center;
  justify-content: flex-start;
  padding-top: 2%;
  margin: 1%;
}

.preview-photo {
  width: 90%;
  height: 10%;
  margin: 1%;
}

.img-container {
  text-align: center;
  justify-content: center;
  align-items: center;
}

.img-detail {
  height: 50vh;
  width: 50wh;
  padding: 1%;
  margin: 1%;
}

.img-detail-wide {
  height: 40vh;
  width: 40wh;
  margin: 1%;
  border: 0.5px solid #008080;
  box-shadow: 5px 9px #c9c9c9;
}

.detail-container {
  text-align: center;
}

a {
  color: #008080
}

.about {
  font-size: 1.2rem;
}

.quote {
  font-size: 1.2rem;
}

.bio {
  font-size: 1.4rem;
}